import React from "react";
import Title from "../Title/Title";
import CardProduct from "../productItem/ProductItem";
import axios from "axios";
export  default async function CategoryMeal() {
  async function getAllCategories(){
    try{
      const options = {
        url: "https://backend-three-nu-89.vercel.app/categories",
        method: "GET"
      };
      const {data} = await axios.request(options)
      console.log(data)
      return data
    }catch(err){
      console.log(err);
    }
  }
  const categories = await getAllCategories();
  console.log(categories)

 
  return (
    <>
      <div className="container">
        <Title titleWord="Burgers" />
        <div className="cards-burgers grid mt-10  grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          <CardProduct />
          <CardProduct />
          <CardProduct />
          <CardProduct />
          <CardProduct />
          <CardProduct />
          <CardProduct />
          <CardProduct />
        </div>
      </div>
    </>
  );
}
